body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.row {
  margin: 0 auto;
}

.jumbotron {
      background-image: url("/images/wallpaper.jpg");
}


.navbar {
    background-color: #3861B0;
    width: 100%;
    display: block;
    font-size: 1.5em;
}

.brand {
    font-weight: bold;
}

.navbar ul {
    margin: 0;
    padding: 0;
}

.navbar li {
    display: inline-block;
    width: 33.33%;
    padding: 10px 0;
    text-align: center;
    color: #FFCB05;
}

.correct {
    -webkit-animation: flashgreen 1s;
            animation: flashgreen 1s;
}

.incorrect {
    -webkit-animation: flashred 1s;
            animation: flashred 1s;
}

@-webkit-keyframes flashgreen {
    0% {color: #FFCB05;}
    50% {color: #00ff00;}
    100% {color: #FFCB05;}
}

@keyframes flashgreen {
    0% {color: #FFCB05;}
    50% {color: #00ff00;}
    100% {color: #FFCB05;}
}

@-webkit-keyframes flashred {
    0% {color: #FFCB05;}
    50% {color: #ff0000;}
    100% {color: #FFCB05;}
}

@keyframes flashred {
    0% {color: #FFCB05;}
    50% {color: #ff0000;}
    100% {color: #FFCB05;}
}

.shake {
  /* Start the shake animation and make the animation last for 0.5 seconds */
  -webkit-animation: shake 0.7s;
          animation: shake 0.7s;
  width: 30em; 
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg);}
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg);}
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}
.jumbotron {
    min-height: 350px;
    background-color: #010101;

    background-repeat: no-repeat;
    background-position: center;
    background-size: auto 350px;
    padding-top: 100px;
    margin-top: 60px;
    padding-bottom: 0;
}

h1,
p {
    text-align: center;
    font-weight: bold;
    color: #FFCB05;
    text-shadow: 0 0 5px #000, 0 0 10px #3861B0, 0 0 20px #3861B0, 0 0 30px #3861B0, 0 0 40px #3861B0, 0 0 50px #3861B0, 0 0 60px #3861B0;
    -moz-text-shadow: 0 0 10px #3861B0, 0 0 20px #3861B0, 0 0 30px #3861B0, 0 0 40px #3861B0, 0 0 50px #3861B0;;
    -webkit-text-shadow: 0 0 10px #3861B0, 0 0 20px #3861B0, 0 0 30px #3861B0, 0 0 40px #3861B0, 0 0 50px #3861B0;;
}

h1 {
    margin-top: -1em;
    font-size: 3.5em;
}

p {
    font-size: 1.5em;
    margin-top: 6em;
}
.card {
    width: 10.5em;
    display: inline-block;
    margin: 1em;
    background: #eee;
}

.card:hover {
    cursor: pointer;
}

@media only screen and (min-width: 893px) {
    .card {
        margin: 1em 0.5em;
    }
}
